import { environment } from "src/environments/environment";
import { appRoutes } from "src/app/config";
import { Router } from "@angular/router";
import { DashboarService } from "./../../@core/dashboard.service";
import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { AuthService, GeneralService } from "src/app/@core";

@Component({
  selector: "app-complete-profile",
  templateUrl: "./complete-profile.component.html",
  styleUrls: ["./complete-profile.component.scss"],
})
export class CompleteProfileComponent implements OnInit {
  constructor(
    private dashboardService: DashboarService,
    private generalService: GeneralService,
    private authService: AuthService,
    private router: Router,
    private toastr: ToastrService
  ) {}
  classes: any;
  subjects: any;
  user: any = {};
  showPage = false;

  countries = environment.countries;

  ngOnInit(): void {
    this.getUserInfo();
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();

    if (this.user.member_subject_list) {
      this.router.navigate([appRoutes.dashboard]);
      return;
    }

    this.dashboardService.getMemberProfile().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;
          Object.assign(
            this.user,
            this.generalService.convertToOneLayerViewModel(data)
          );
          this.authService.saveUserLocally(this.user);

          if (data.member_subject_list) {
            this.router.navigate([appRoutes.dashboard]);
          } else {
            this.showPage = true;

            this.getClasses();
            this.getSubjects();
          }
        } else {
          this.showPage = true;
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.showPage = true;
        this.toastr.error("Something went wrong");
      }
    );
  }

  getClasses() {
    this.dashboardService.getAllClasses().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;

          this.classes = data.list.class;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSubjects() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllSubjects().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.subjects = data.list.subject;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  // getSubjectsForSelectedClasses(classNameArr: { value: Array<string> }) {
  //   // TODO:this presently returns all subjects change to return specific subjects
  //   this.dashboardService
  //     .getAllSubjectsForClass(classNameArr.value.join())
  //     .subscribe(
  //       (data) => {
  //         if (data.status._text !== "Failed") {
  //           delete data.status._text;

  //           this.subjects = data.list.subject;
  //         } else {
  //           this.toastr.error(data.status_message._text, "Oops");
  //         }
  //       },
  //       (error) => {
  //         this.toastr.error("Something went wrong");
  //       }
  //     );
  // }

  updateUsersProfile(user) {
    this.generalService.showLoader(true);
    const savedUser = this.authService.getUserLocally();
    this.user.first_name = savedUser.first_name;
    this.user.surname = savedUser.surname;
    this.dashboardService.getMemberProfileUpdate(user).subscribe(
      (data) => {
        if (data.status._text !== "Failed") {
          delete data.status._text;
          this.user.member_class_list = this.user.class?.toString();
          this.user.member_subject_list = this.user.subject?.toString();

          Object.assign(savedUser, user);
          this.authService.saveUserLocally(savedUser);
          this.generalService.showLoader(false);
          this.toastr.success("Your profile has been updated");
          this.router.navigate([appRoutes.dashboard]);
        } else {
          this.toastr.error(data.status_message._text, "Oops");
          this.generalService.showLoader(false);
        }
      },
      (error) => {
        this.toastr.error("Something went wrong", "Oops");
      }
    );
  }
}
