import {
  MOBILE_MENU,
  ONBOARDING_FLOW,
  MOBILE_ONBOARDING_FLOW,
} from "./../page/pages.menu";
import { DashboarService } from "src/app/@core/dashboard.service";
import { ToastrService } from "ngx-toastr";
import { Router, NavigationEnd, ActivatedRoute } from "@angular/router";
import { appRoutes } from "src/app/config";
import { Component, OnInit, OnDestroy, HostListener } from "@angular/core";
import { MENU } from "../page/pages.menu";
import { GeneralService, AuthService } from "src/app/@core";
import { Subscription } from "rxjs";
import { MatDialog } from "@angular/material/dialog";
import { JoyrideService } from "ngx-joyride";

@Component({
  selector: "app-container",
  templateUrl: "./container.component.html",
  styleUrls: ["../page/page.component.scss", "./container.component.scss"],
})
export class ContainerComponent implements OnInit, OnDestroy {
  menuItems = MENU;
  mobileMenu = MOBILE_MENU;
  loading: boolean;
  loadingSubscription: Subscription;

  personalizedRoute = appRoutes.personalizedContent;
  routeSubscription: Subscription;
  hasNewPC: boolean;
  checkPCtimeout: any;
  user: any;

  rating: number = 0;

  constructor(
    private generalService: GeneralService,
    private router: Router,
    public dialog: MatDialog,
    private toastr: ToastrService,
    private dashboardService: DashboarService,
    private authService: AuthService,
    private joyrideService: JoyrideService
  ) {}

  ngOnInit(): void {
    this.checkForLoadingState();
    this.checkForRouteChanges();
    this.checkForNewPC();
    this.getProfileInfo();
    this.showFreeSubPopUp();
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    this.showTutorialIfNeeded();
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.

    clearTimeout(this.checkPCtimeout);
  }

  showOnboardingIfUserhasntSeenIt() {
    const user = this.authService.getUserLocally();
    const hasUserSeenOnboard = user.first_login !== "TRUE";

    if (!hasUserSeenOnboard && localStorage.getItem("__onboard") !== "2") {
      localStorage.setItem("__onboard", "2");
      const body: any = document.querySelector("body");
      body.style = "overflow: hidden;";

      document.getElementById("startOnboard").click();
      Object.assign(user, { first_login: "FALSE" });
      this.authService.saveUserLocally(user);
    }
  }

  startTour() {
    const steps =
      screen.width > 1024 ? ONBOARDING_FLOW : MOBILE_ONBOARDING_FLOW;
    this.joyrideService
      .startTour({
        steps,
        themeColor: "#387249",
        customTexts: {
          next: "Next",
          prev: "Back",
          done: "Done",
        },
      })
      .subscribe(
        (step) => {
          /*Do something*/
        },
        (error) => {
          /*handle error*/
          console.log({ error });
        },
        () => {
          const body: any = document.querySelector("body");
          body.style = "";
        }
      );
  }

  checkForLoadingState() {
    this.loadingSubscription = this.generalService.state$.subscribe((arg) => {
      this.loading = arg;
    });
  }

  checkForNewPC() {
    if (this.hasNewPC !== false) {
      clearTimeout(this.checkPCtimeout);
      this.getPersonalizedContentStatus();
      this.checkPCtimeout = setTimeout(() => {
        this.checkForNewPC();
      }, 3000);
    } else {
      clearTimeout(this.checkPCtimeout);
    }
  }

  showFreeSubPopUp() {
    const user = this.authService.getUserLocally();

    if (user.first_login === "TRUE" && !localStorage.getItem("__onboard")) {
      document.getElementById("free-sub").click();
      localStorage.setItem("__onboard", "1");
    }
  }

  congratsClosed() {
    if (this.router.url.includes(appRoutes.dashboard)) {
      window.scrollTo({
        top: 10,
        behavior: "smooth",
      });

      setTimeout(() => {
        this.showOnboardingIfUserhasntSeenIt();
      }, 1000);
    }
  }

  showTutorialIfNeeded() {
    if (this.router.url.includes(appRoutes.dashboard)) {
      if (this.dialog.openDialogs.length === 0) {
        window.scrollTo({
          top: 10,
          behavior: "smooth",
        });
        this.showOnboardingIfUserhasntSeenIt();
      }
    }
  }

  checkForRouteChanges() {
    this.routeSubscription = this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationEnd) {
        this.checkForNewPC();
        this.getProfileInfo();
        window.scrollTo({
          top: 10,
          behavior: "smooth",
        });
        this.showTutorialIfNeeded();
      }

      return;
    });
  }

  getProfileInfo() {
    this.user = this.authService.getUserLocally();
    this.setProfileimage();
    this.dashboardService.getMemberProfile().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;
          Object.assign(
            this.user,
            this.generalService.convertToOneLayerViewModel(data)
          );
          this.authService.saveUserLocally(this.user);
          this.setProfileimage();
        }
      },
      (error) => {
        // this.toastr.error("Something went wrong");
      }
    );
  }

  setProfileimage() {
    if (this.user) {
      if (this.user.passport) {
        let circleEl: any;
        circleEl = document.getElementsByClassName("circle")[0];
        circleEl.style = `background-image: url(${this.user.passport}); background-size: cover`;
        circleEl.addEventListener("click", () =>
          this.router.navigate([appRoutes.settings])
        );
      }
    }
  }

  openModal(template: any, id = "") {
    this.dialog.open(template, {
      disableClose: true,
      id,
    });
  }

  onRate(rating: number) {
    this.dashboardService.saveUsersFeedback(rating).subscribe();
    this.toastr.success("Thanks for your feedback");
    this.rating = rating;
  }

  logOut() {
    this.authService.logout();
    this.router.navigate([appRoutes.login]);
  }

  getPersonalizedContentStatus() {
    return this.dashboardService.getPersonalisedContentStatus().subscribe(
      (data) => {
        if (data.status._text === "Success") {
          delete data.status._text;

          this.hasNewPC =
            data.viewed_all_content._text.toLowerCase() === "false"
              ? false
              : true;
        } else {
          // this.toastr.error(data.status_message?._text, "Oops");
          this.hasNewPC = false;
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
        this.hasNewPC = false;
      }
    );
  }
}
