import { PersonalizedContentComponent } from "./personalized-content/personalized-content.component";
import { SavedItemsComponent } from "./saved-items/saved-items.component";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import {
  PageComponent,
  DashboardComponent,
  CompleteProfileComponent,
  SearchBoxComponent,
} from ".";
import { SearchPageComponent } from "./search-page/search-page.component";
import { ViewPlanComponent } from "./view-plan/view-plan.component";
import { SettingsComponent } from "./settings/settings.component";
import { ContainerComponent } from "./container/container.component";

const routes: Routes = [
  {
    path: "",
    component: ContainerComponent,
    children: [
      {
        path: "",
        component: PageComponent,
        children: [
          { path: "", pathMatch: "full", redirectTo: "complete-profile" },
          {
            path: "s",
            component: SearchBoxComponent,
            children: [
              { path: "dashboard", component: DashboardComponent },
              { path: "search-result", component: SearchPageComponent },
              { path: "search/:category/:id", component: ViewPlanComponent },
              {
                path: "search/:category/:id/:innovative-id",
                component: ViewPlanComponent,
              },
              { path: "", pathMatch: "full", redirectTo: "dashboard" },
            ],
          },

          { path: "complete-profile", component: CompleteProfileComponent },
        ],
      },
      {
        path: "subscription",
        loadChildren: () =>
          import("./subscriptions/subscription.module").then(
            (m) => m.SubscriptionModule
          ),
      },
      { path: "settings", component: SettingsComponent },
      { path: "saved-items", component: SavedItemsComponent },
      {
        path: "personalized-contents",
        component: PersonalizedContentComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PageRoutingModule {}
export const routedComponent = [
  PageComponent,
  SearchPageComponent,
  DashboardComponent,
  SearchBoxComponent,
  SettingsComponent,
  CompleteProfileComponent,
  ViewPlanComponent,
  ContainerComponent,
  SavedItemsComponent,
  PersonalizedContentComponent,
];
