import { Component, OnInit } from "@angular/core";
import { AuthService, GeneralService } from "src/app/@core";
import { User } from "src/app/@model";
import { DashboarService } from "src/app/@core/dashboard.service";
import { Router, ROUTES } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { environment } from "src/environments/environment";
import { MatDialog } from "@angular/material/dialog";
import { DomSanitizer } from "@angular/platform-browser";
import { appRoutes } from "src/app/config";

@Component({
  selector: "app-settings",
  templateUrl: "./settings.component.html",
  styleUrls: [
    "../../@auth/auth/auth.component.scss",
    "./settings.component.scss",
  ],
})
export class SettingsComponent implements OnInit {
  classes: any;
  subjects: any;
  user: any = {};
  countries = environment.countries;
  contentToClear = "";

  hidePassword = true;
  hideConfirmPassword = true;
  hideOldPassword = true;
  shouldSendNewLetter: number;
  selectedPhoto: any;
  imageUrl: any;
  constructor(
    private dashboardService: DashboarService,
    private generalService: GeneralService,
    public authService: AuthService,
    private sanitizer: DomSanitizer,
    private router: Router,
    public dialog: MatDialog,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.getUserInfo();
    this.getClasses();
    this.getSubjects();
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();
    this.setProfileimage();
    this.dashboardService.getMemberProfile().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;

          Object.assign(
            this.user,
            this.generalService.convertToOneLayerViewModel(data)
          );
          this.authService.saveUserLocally(
            Object.assign(this.authService.getUserLocally(), this.user)
          );

          this.setProfileimage();

          this.user.class = this.user.member_class_list
            .replace(" ", "")
            .split(",");
          this.user.subject = this.user.member_subject_list
            .replace(" ", "")
            .split(",");

          this.imageUrl = this.user.passport;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  setProfileimage() {
    let circleEl: any;
    circleEl = document.getElementsByClassName("circle")[0];
    circleEl.style = `background-image: url(${this.user.passport}); background-size: cover`;
    circleEl.addEventListener("click", () =>
      this.router.navigate([appRoutes.settings])
    );
  }

  getClasses() {
    this.dashboardService.getAllClasses().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.classes = data.list.class;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSubjects() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllSubjects().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.subjects = data.list.subject;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  clearContent(contentToClear) {
    if (contentToClear === "saved items") {
      this.clearSavedItems();
    } else {
      this.clearPersonalizedItems();
    }
  }

  clearSavedItems() {
    this.generalService.showLoader(true);
    this.dashboardService.clearSavedItems().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        this.dialog.closeAll();
        if (data.status._text != "Failed") {
          this.toastr.success("Saved Items cleared");
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  clearPersonalizedItems() {
    this.generalService.showLoader(true);
    this.dashboardService.clearPersonalizedContent().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        this.dialog.closeAll();
        if (data.status._text != "Failed") {
          this.toastr.success("Personalized content cleared");
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  openModal(template: any, contentType = "") {
    this.contentToClear = contentType;
    this.dialog.open(template, {
      disableClose: true,
      id: "confirm-box5",
    });
  }

  getSubjectsForSelectedClasses(classNameArr: { value: Array<string> }) {
    // TODO:this presently returns all subjects change to return specific subjects
    this.dashboardService
      .getAllSubjectsForClass(classNameArr.value.join())
      .subscribe(
        (data) => {
          if (data.status._text !== "Failed") {
            delete data.status._text;

            this.subjects = data.list.subject;
          } else {
            this.toastr.error(data.status_message._text, "Oops");
          }
        },
        (error) => {
          this.toastr.error("Something went wrong");
        }
      );
  }

  toggledNewsletter(event) {
    this.shouldSendNewLetter = event.checked ? 2 : 1;
  }

  updateNotificationSetting() {
    this.dashboardService.newsletterSetting(this.shouldSendNewLetter).subscribe(
      (data) => {
        this.generalService.showLoader(false);

        if (data.status._text != "Failed") {
          this.toastr.success("Notifications setting updated");
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  selectPhoto(event) {
    let file = event.target.files[0];
    if (file) {
      let acceptedTypes = ["image/jpeg", "image/png"];

      if (!acceptedTypes.includes(file.type)) {
        this.selectedPhoto = null;
        return this.toastr.error(
          "Error, only jpeg or png images are allowed please."
        );
      }

      if (file.size > 2000000) {
        this.selectedPhoto = null;
        return this.toastr.error(
          "Error, the maximum file size you can upload is 2MB."
        );
      }

      this.selectedPhoto = file;

      this.imageUrl = this.sanitizer.bypassSecurityTrustResourceUrl(
        URL.createObjectURL(this.selectedPhoto)
      );
    }
  }

  changeUserPassword(user) {
    this.generalService.showLoader(true);
    this.dashboardService.changeMemberPassword(user).subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.toastr.success("Password changed successfully");
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  updateUsersProfile(user) {
    delete user.member_subject_list;
    delete user.member_class_list;

    user.user_type = user.user_category;
    const localuser = this.authService.getUserLocally();
    user.class = typeof user.class != "string" ? user.class.join() : user.class;
    user.subject =
      typeof user.subject != "string" ? user.subject.join() : user.subject;
    user.token = localuser.token;
    user.user_id = localuser.user_id;
    delete user.passport;
    delete user.status;

    this.generalService.showLoader(true);

    this.updateUser(user);
  }

  uploadPicture() {
    this.generalService.showLoader(true);
    const user: { pix?: any; token?: string; user_id?: string } = {};
    user.pix = this.selectedPhoto;
    user.token = this.authService.getUserToken();
    user.user_id = this.authService.getUserLocally().user_id;

    this.dashboardService.getMemberProfileUpdateWithPhoto(user).subscribe(
      (data) => {
        if (data.status == "Success") {
          this.generalService.showLoader(false);
          this.toastr.success(data.status_message);
          this.getUserInfo();
        } else {
          this.toastr.error(data.status_message, "Oops");
          this.generalService.showLoader(false);
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong", "Oops");
      }
    );
  }

  updateUser(user) {
    user.user_type = user.user_category;
    this.dashboardService.getMemberProfileUpdate(user).subscribe(
      (data) => {
        if (data.status._text !== "Failed") {
          delete data.status._text;

          this.authService.saveUserLocally(user);
          this.generalService.showLoader(false);
          this.toastr.success("Your profile has been updated");
        } else {
          this.toastr.error(data.status_message._text, "Oops");
          this.generalService.showLoader(false);
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong", "Oops");
      }
    );
  }

  changeNewsLetterSubscriptionStatus() {
    //TODO: call newletter endpoint
  }
}
