import { appRoutes } from "src/app/config";
import { DashboarService } from "./../../@core/dashboard.service";
import { GeneralService } from "./../../@core/general.service";
import { ActivatedRoute, Router } from "@angular/router";
import {
  Component,
  OnInit,
  TemplateRef,
  ViewChild,
  OnDestroy,
  HostListener,
} from "@angular/core";
import { Location } from "@angular/common";
import { ToastrService } from "ngx-toastr";
import { MatDialogRef, MatDialog } from "@angular/material/dialog";
import { DomSanitizer } from "@angular/platform-browser";
import { innovativeContentSection1XML } from "./view-plan.mockdata";

import {
  NgxImageGalleryComponent,
  GALLERY_IMAGE,
  GALLERY_CONF,
} from "ngx-image-gallery";
import { environment } from "src/environments/environment";

@Component({
  selector: "app-view-plan",
  templateUrl: "./view-plan.component.html",
  styleUrls: [
    "../dashboard/dashboard.component.scss",
    "./view-plan.component.scss",
  ],
})
export class ViewPlanComponent implements OnInit, OnDestroy {
  id: string;
  scrolledCount: any = {};
  category: string;
  pictures: { _text: string }[];
  modalRef: MatDialogRef<any>;
  sanitizedURL: any;
  downloadLink: string;
  planImages: Array<string>;
  planWebURLs = {};
  plan: any;
  routes = appRoutes;
  categoryTypes = environment.categoryTypes;

  // gallery configuration
  conf: GALLERY_CONF = {
    imageOffset: "0px",
    showDeleteControl: false,
    showImageTitle: false,
    showExtUrlControl: false,
  };

  // gallery images
  images: GALLERY_IMAGE[] = [];
  presentComponent: number;
  componentHasImage: boolean;
  displayImages: any;
  suggestedTopics: any[] = [];

  contentKeys = {
    ITMCA: 1,
    IRM: 2,
    PEC: 3,
    IIET: 4,
  };
  contentTypeColor: string;
  subjects = [];
  hasSubExpired: boolean = false;
  satisfactionCount: number = 0;
  shouldCheckSatisfaction: boolean = true;
  showArrow: boolean;

  constructor(
    private router: Router,
    public dialog: MatDialog,
    private dashboardService: DashboarService,
    private route: ActivatedRoute,
    private sanitizer: DomSanitizer,
    private generalService: GeneralService,
    private toastr: ToastrService,
    private location: Location
  ) { }

  // get reference to gallery component
  @ViewChild(NgxImageGalleryComponent)
  ngxImageGallery: NgxImageGalleryComponent;

  ngOnInit(): void {
    this.getURLparams();
  }

  shouldShowArrow() {
    const innovativeModalEl: any = document.getElementsByClassName(
      "mat-dialog-content"
    )[0];
    this.showArrow = !(
      innovativeModalEl.scrollTop + innovativeModalEl.clientHeight + 100 >=
      innovativeModalEl.scrollHeight
    );
  }

  getURLparams() {
    this.route.paramMap.subscribe((param) => {
      this.suggestedTopics = [];
      this.id = param.get("id");

      this.category = param.get("category").toLowerCase();

      this.getSubjects();
      this.shouldCheckSatisfaction = true;
    });
  }

  saveContentId(id: string, isLessonPlan = true) {
    this.dashboardService.saveContentId(isLessonPlan, id);
  }

  getLessonPlanById(planId) {
    this.generalService.showLoader(true);
    this.generalService.pingContentSource({ isLessonPlan: true, id: this.id });
    this.dashboardService.getLessonPlanById(planId).subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          if (data.status._text != "No Subscription") {
            this.hasSubExpired = false;
            delete data.status._text;
            this.plan = data.list.lesson_plan;
            this.saveContentId(this.id);
            this.sanitizedURL = this.sanitizer.bypassSecurityTrustResourceUrl(
              this.plan.doc_pdf._text
            );
            this.pictures = Object.values(this.plan.lesson_pictures);
            this.getSuggestionsPlans();
          } else {
            this.toastr.warning("Subscribe to view this Content");
            this.hasSubExpired = true;
          }
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSuggestionsPlans() {
    const subject = this.subjects.find((x) => {
      return x._text === this.plan.subject_name._text;
    })._attributes.subject_id;

    this.suggestedTopics = [];
    const query = {
      topic: this.plan.topic._text,
      subject,
    };
    this.dashboardService.getSmartSearch(query).subscribe(
      (data) => {
        const innovativeContent = {};
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status;

          const lessonPlan = data.search_list.lesson_plan
            ? [].concat(data.search_list.lesson_plan)
            : [];

          lessonPlan.forEach((plan) => {
            plan.category = "lesson plan";
            plan.badgeColor = "bg-success";

            plan.pictures = Object.values(plan.lesson_pictures).map(
              (x: any) => x._text
            );
          });

          if (data.search_list.innovative_content) {
            let invContent = data.search_list.innovative_content
              ? [].concat(data.search_list.innovative_content)
              : [];

            const newContentArr: any = invContent.map((x) => {
              x.component.map((y) => {
                y.lesson_id = x._attributes.lesson_id;
                y.topic = x._attributes.topic;
                y.class_name = x._attributes.class_name;
              });

              return x.component;
            });
            invContent = newContentArr.flat();
            for (let i = 0; i < invContent.length; i++) {
              const innoComp = invContent[i]._attributes.short_name;
              if (!innovativeContent[innoComp]) {
                innovativeContent[innoComp] = [];
              }
              invContent[i].category = innoComp;
              invContent[i].pictures = invContent[i].contents.images
                ? invContent[i].contents.images._text.split(",")
                : "/assets/images/logo.png";
              invContent[
                i
              ].badgeColor = this.generalService.getInnovativeContentBadgeColor(
                innoComp
              );
              innovativeContent[innoComp].push(invContent[i]);
            }
            this.suggestedTopics = lessonPlan.concat(
              Object.values(innovativeContent)[0]
            );
          }
        } else {
          // this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getUserSatisfactionCount(id: string, isLessonPlan: boolean) {
    this.dashboardService
      .getSatisfactedCount({
        data_id: id,
        data_type: isLessonPlan ? 1 : 2,
      })
      .subscribe((data) => {
        if (!data.counts) {
          this.satisfactionCount = 0;
        } else {
          this.satisfactionCount = +data.counts.satisfied._text;
        }
      });
  }

  getUserContentSatisfaction(id: string, isLessonPlan: boolean) {
    this.generalService.contentLikedState$.subscribe((data) => {
      if (data && this.shouldCheckSatisfaction) {
        this.satisfactionCount++;
        this.shouldCheckSatisfaction = false;
      }
    });

    this.dashboardService
      .getUserSatisfactory({
        data_id: id,
        data_type: isLessonPlan ? 1 : 2,
      })
      .subscribe((data) => {
        if (data.status_message._text.toLowerCase() == "satisfied") {
          this.shouldCheckSatisfaction = false;
        }
      });
  }

  saveContentSatisfaction(isSatisfied) {
    const contentInfo: any = this.dashboardService.getContentIdInfo();
    if (this.shouldCheckSatisfaction) {
      if (isSatisfied) {
        this.satisfactionCount++;
        this.shouldCheckSatisfaction = false;
      }
      if (contentInfo.isLessonPlan) {
        this.dashboardService
          .lessonPlanSatisfactory({
            lesson: contentInfo.id,
            satisfied: isSatisfied ? 2 : 1,
          })
          .subscribe(() => {
            this.toastr.success("Thank you for your feedback");
          });
      } else {
        this.dashboardService
          .InnovativeContentSatisfactory({
            innovative_id: contentInfo.id,
            satisfied: isSatisfied ? 2 : 1,
          })
          .subscribe(() => {
            this.toastr.success("Thank you for your feedback");
          });
      }
    }
  }

  showTheRightContent() {
    if (this.category == "lesson plan") {
      this.getLessonPlanById(this.id);
      this.getUserSatisfactionCount(this.id, true);
      this.getUserContentSatisfaction(this.id, true);
    } else {
      this.getInnovativeContentById(
        this.id,
        this.contentKeys[this.category.trim().toUpperCase()]
      );

      this.contentTypeColor = this.generalService.getInnovativeContentBadgeColor(
        this.category.trim()
      );
    }
  }

  getSubjects() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllSubjects().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.subjects = data.list.subject;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
        this.showTheRightContent();
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  openRightModal(
    lessonPlanOrDownloadTemplate: TemplateRef<any>,
    innovativeContentTemplate: TemplateRef<any>,
    isContent = true
  ) {
    if (isContent === false) {
      this.dialog.open(lessonPlanOrDownloadTemplate, {
        height: "250px",
        width: "350px",
      });
    } else if (this.category == "lesson plan") {
      this.dialog.open(lessonPlanOrDownloadTemplate, {
        height: "80vh",
        width: "100%",
        disableClose: true,
        closeOnNavigation: true,
      });
    } else {
      this.dialog.open(innovativeContentTemplate, {
        height: "80vh",
        width: "100%",
        disableClose: true,
        id: "innovative-content-box",
      });
    }
  }

  ngOnDestroy(): void {
    this.dashboardService.clearsContentId();
  }

  openMaxDownloadModal(template) {
    this.dialog.open(template, {
      id: "confirm-box6",
    });
  }

  download(link: string, type: string) {
    this.dashboardService.saveDownloadedPlan(type, this.id).subscribe(
      (data) => {
        if (data.status._text == "Success") {
          this.downloadContentFromLink(link);
        } else if (data.status._text.toLowerCase() === "maxdownload") {
          document.getElementById("maxDownload").click();
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  downloadInnovativeContent(link) {
    this.dashboardService
      .saveDownloadedInnovativeContent(this.plan._attributes.innovative_id)
      .subscribe();
    // this.downloadLink = `https://cors-anywhere.herokuapp.com/${link}`;
    // document.getElementById("downloadContent").click();

    this.downloadContentFromLink(link);
  }

  downloadContentFromLink(link: string) {
    const plan = this.plan;
    // var xhr = new XMLHttpRequest();
    // xhr.open("GET", `${link}`, true);
    // xhr.responseType = "blob";
    // xhr.onload = function () {
    //   var urlCreator = window.URL || window.webkitURL;
    //   var imageUrl = urlCreator.createObjectURL(this.response);
    //   var tag = document.createElement("a");
    //   tag.href = imageUrl;
    //   tag.download = plan.topic._text;
    //   document.body.appendChild(tag);
    //   tag.click();
    //   document.body.removeChild(tag);
    // };
    // xhr.send();    
    var tag = document.createElement("a");
    tag.href = link;
    tag.download = plan.topic._text;
    document.body.appendChild(tag);
    tag.click();
    document.body.removeChild(tag);
  }

  openLink(link: string) {
    window.open(link, "_blank");
  }

  goBack() {
    this.location.back();
  }

  isImage(link: string) {
    return (
      link.endsWith(".jpg") ||
      link.endsWith(".jpeg") ||
      link.endsWith(".png") ||
      link.endsWith(".gif")
    );
  }

  copyToClipboard() {
    const el = document.createElement("textarea");
    el.value = location.href;
    el.setAttribute("readonly", "");
    el.style.position = "absolute";
    el.style.left = "-9999px";
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);

    this.toastr.success("Link has been copied to clipboard");
  }

  getInnovativeContentById(innovativeContentId, compNo = 1) {
    this.generalService.showLoader(true);
    this.dashboardService
      .getInnovativeContentById(innovativeContentId, compNo)
      .subscribe(
        (data) => {
          this.presentComponent = compNo;
          this.planWebURLs = {};
          delete this.displayImages;

          this.generalService.showLoader(false);
          if (data.status._text != "Failed") {
            if (data.status._text != "No Subscription") {
              this.hasSubExpired = false;
              delete data.status._text;
              this.plan = data.innovative_content;
              this.getUserSatisfactionCount(
                this.plan._attributes.innovative_id,
                false
              );
              this.getUserContentSatisfaction(
                this.plan._attributes.innovative_id,
                false
              );
              this.generalService.pingContentSource({
                isLessonPlan: false,
                id: this.plan._attributes.innovative_id,
              });
              this.saveContentId(this.plan._attributes.innovative_id, false);
              if (data.innovative_content.contents.images) {
                this.componentHasImage = true;
                this.planImages = [];
                this.images = [];
                const imageTypesArr = data.innovative_content.contents.images._attributes.useable_app.split(
                  ","
                );
                const imageUrlArr = data.innovative_content.contents.images._text.split(
                  ","
                );
                imageTypesArr.forEach((item, index) => {
                  item = item.toLowerCase();

                  this.planImages.push(imageUrlArr[index].trimStart());

                  this.images.push({
                    url: imageUrlArr[index].trimStart(),
                  });

                  if (!this.displayImages) {
                    this.displayImages = this.planImages;
                  }
                });
              } else {
                this.componentHasImage = false;
              }
              if (data.innovative_content.contents.weburl) {
                const webUrlTypesArr = data.innovative_content.contents.weburl._attributes.useable_app.split(
                  ","
                );
                const webUrlArr = data.innovative_content.contents.weburl._text.split(
                  ","
                );

                webUrlTypesArr.forEach((item, index) => {
                  item = item.toLowerCase();
                  if (!this.planWebURLs[item]) {
                    this.planWebURLs[item] = [];
                  }
                  this.planWebURLs[item].push(webUrlArr[index].trimStart());
                });
              }
              this.getSuggestionsPlans();
            } else {
              this.toastr.warning("Subscribe to view this Content");
              this.hasSubExpired = true;
            }
          } else {
            this.toastr.error(data.status_message._text, "Oops");
          }
        },
        (error) => {
          this.generalService.showLoader(false);
          this.toastr.error("Something went wrong");

          // let data = this.generalService.xmlToJson(
          //   innovativeContentSection1XML
          // );
          // this.generalService.showLoader(false);
          // if (data.status._text != "Failed") {
          //   delete data.status._text;
          //   this.plan = data.innovative_content;
          //   const imageTypesArr = data.innovative_content.contents.images._attributes.useable_app.split(
          //     ","
          //   );
          //   const imageUrlArr = data.innovative_content.contents.images._text.split(
          //     ","
          //   );

          //   const webUrlTypesArr = data.innovative_content.contents.weburl._attributes.useable_app.split(
          //     ","
          //   );
          //   const webUrlArr = data.innovative_content.contents.weburl._text.split(
          //     ","
          //   );

          //   imageTypesArr.forEach((item, index) => {
          //     item = item.toLowerCase();

          //     this.planImages.push(imageUrlArr[index].trimStart());
          //   });

          //   webUrlTypesArr.forEach((item, index) => {
          //     item = item.toLowerCase();
          //     if (!this.planWebURLs[item]) {
          //       this.planWebURLs[item] = [];
          //     }
          //     this.planWebURLs[item].push(webUrlArr[index].trimStart());
          //   });
          // }
        }
      );
  }

  /**
   * Gets the domain of the provided URL
   * @param url the url we want to pick the domain from
   * @returns the derived domain from the URL
   */
  getDomain(url: string): string {
    return url.split("://")[1].split("/")[0].replace("www.", "");
  }

  getKeys(object): Array<string> {
    return Object.keys(object);
  }

  openFullscreen() {
    const elem: any = document.documentElement;
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.mozRequestFullScreen) {
      /* Firefox */
      elem.mozRequestFullScreen();
    } else if (elem.webkitRequestFullscreen) {
      /* Chrome, Safari & Opera */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) {
      /* IE/Edge */
      elem.msRequestFullscreen();
    }
  }

  openGallery(index: number = 0) {
    this.ngxImageGallery.open(index);
    this.openFullscreen();
  }

  // close gallery
  closeGallery() {
    this.ngxImageGallery.close();
  }

  galleryClosed() {
    document.exitFullscreen();
  }

  // next image in gallery
  nextImage(index: number = 0) {
    this.ngxImageGallery.next();
  }

  // prev image in gallery
  prevImage(index: number = 0) {
    this.ngxImageGallery.prev();
  }

  scrollHorizontal(event, key) {
    if (!this.scrolledCount[key]) {
      this.scrolledCount[key] = 1;
    }
    const containerDiv = event.currentTarget.parentElement.children[0];
    containerDiv.scrollTo(
      containerDiv.offsetWidth * this.scrolledCount[key] - 150,
      0
    );
    this.scrolledCount[key]++;
  }
}
