import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment";
import { GeneralService } from "./general.service";

@Injectable({ providedIn: "root" })
export class DashboarService {
  constructor(
    private httpClient: HttpClient,
    private generalService: GeneralService
  ) {}

  /**
   * description: gets all the subjects
   */
  getAllSubjects(): Observable<any> {
    return this.httpClient.get(`${environment.baseURL}/all-subject`, <Object>{
      responseType: "text/xml",
    }) as Observable<any>;
  }

  /**
   * Get list 0f availble personalized data
   */
  getPersonalisedList() {
    return this.httpClient.get(
      `${environment.baseURL}/personalized-content-item-list`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Get list 0f personalized requests
   */
  getPersonalisedRequestList() {
    return this.httpClient.get(
      `${environment.baseURL}/personalized-content-order-list`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }
  /**
   * Get Personalized content status showing if all content ha been viewed or not
   */
  getPersonalisedContentStatus() {
    return this.httpClient.get(
      `${environment.baseURL}/personalized-content-status`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * get lesson plan by id
   */
  getLessonPlanById(id): Observable<any> {
    return this.httpClient.get(
      `${environment.baseURL}/get-lesson-plan-data?lesson=${id}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * get lesson plan by id
   */
  getInnovativeContentById(id, component = 1): Observable<any> {
    return this.httpClient.get(
      `${environment.baseURL}/search-innovative-detail?lesson=${id}&component=${component}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * description: gets all the classes
   */
  getAllClasses(): Observable<any> {
    return this.httpClient.get(`${environment.baseURL}/all-class`, <Object>{
      responseType: "text/xml",
    }) as Observable<any>;
  }

  /**
   * description: gets all subject for specifed classes
   */
  getAllSubjectsForClass(className): Observable<any> {
    return this.httpClient.get(
      `${environment.baseURL}/class-subject?class=${className}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Change Users Password
   * @param user user's password and new password
   */
  changeMemberPassword(user: any) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/member-change-password${this.generalService.generateSearchQuery(user)}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * description: gets all content update
   */
  getContentUpdate(): Observable<any> {
    return this.httpClient.get(`${environment.baseURL}/notification`, <Object>{
      responseType: "text/xml",
    }) as Observable<any>;
  }

  /**
   * Update User's profile
   */
  getMemberProfileUpdate(userProfile): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/member-profile-edit${this.generalService.generateSearchQuery(
        userProfile
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Update User's profile with picture
   */
  getMemberProfileUpdateWithPhoto(user: any) {
    return this.httpClient.post(
      `${environment.baseURL}/member-picture-upload`,
      this.generalService.objectToFormData(user),
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Gets the logged in users profile
   */
  getMemberProfile() {
    return this.httpClient.get(`${environment.baseURL}/member-profile`, <
      Object
    >{ responseType: "text/xml" }) as Observable<any>;
  }

  /*
   * Gets Lesson plan
   */
  getLessonPlan(seachQuery): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/search-lesson-plan${this.generalService.generateSearchQuery(
        seachQuery
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /*
   * Gets innovative content
   */
  getInnovativeContent(seachQuery): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/search-innovative${this.generalService.generateSearchQuery(
        seachQuery
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Gets innovative components or categories
   */
  getInnovativeComponents(): Observable<any> {
    return this.httpClient.get(
      `${environment.baseURL}/get-innovative-component`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Get Suggestions
   * @param keyword the word to which we want to add suggestions
   */
  getSuggestions(keyword): Observable<any> {
    return this.httpClient.get(
      `${environment.baseURL}/smart-suggestion?keyword=${keyword}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Get Smart Search
   * @param searchQuery search topic, class name and subject
   */
  getSmartSearch(searchQuery): Observable<any> {
    searchQuery.keyword = searchQuery.topic;
    delete searchQuery.topic;
    return this.httpClient.get(
      `${
        environment.baseURL
      }/smart-search${this.generalService.generateSearchQuery(searchQuery)}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Save Downloaded Lesson Plan
   * @param type downloaded file type
   * @param id lesson plan id
   */
  saveDownloadedPlan(type: string, id: string) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/save-download-lesson-${type.toLowerCase()}?lesson=${id}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Save users feeback
   */
  saveUsersFeedback(rating: number) {
    return this.httpClient.get(
      `${environment.baseURL}/save-feedback-rating?rating=${rating}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Changes newsletter settings
   */
  newsletterSetting(shouldSendNewLetter: number) {
    return this.httpClient.get(
      `${environment.baseURL}/user-newsletter-setting?newsletter=${shouldSendNewLetter}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Save Downloaded Innovative Content
   * @param id lesson plan id
   */
  saveDownloadedInnovativeContent(id: string) {
    return this.httpClient.get(
      `${environment.baseURL}/save-download-innovative?innovative_id=${id}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Gets a list of lesson plans the user has downloaded
   */
  getSavedLessonPlans() {
    return this.httpClient.get(
      `${environment.baseURL}/get-download-lesson-plan`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Gets a list of Innovative content the user has downloaded
   */
  getSavedInnovativeContent() {
    return this.httpClient.get(
      `${environment.baseURL}/get-download-innovative-content`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Get Recent;y Viewed Items
   */
  getRecentlyViewed() {
    return this.httpClient.get(`${environment.baseURL}/get-recent-view`, <
      Object
    >{ responseType: "text/xml" }) as Observable<any>;
  }

  /**
   * Get Cost to pay for personlaized content
   */
  getPersonlaizedContetnCost() {
    return this.httpClient.get(
      `${environment.baseURL}/personalized-content-cost`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }
  /**
   * Save Lesson plan satisfaction
   * @param data data contanining id and satisfaction rate
   */
  lessonPlanSatisfactory(data: { lesson: any; satisfied: number }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/save-lesson-download-satisfaction${this.generalService.generateSearchQuery(
        data
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * returnd if the ser found this content satisfactory, not satisfied and neither
   * @param data
   */
  getUserSatisfactory(data: { data_id: any; data_type: number }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/check-user-content-satisfaction${this.generalService.generateSearchQuery(
        data
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Gets the number of people who have like a content
   * @param data
   */
  getSatisfactedCount(data: { data_id: any; data_type: number }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/get-content-satisfaction-count${this.generalService.generateSearchQuery(
        data
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Save Innovative content satisfaction
   * @param data data contanining id and satisfaction rate
   */
  InnovativeContentSatisfactory(data: {
    innovative_id: any;
    satisfied: number;
  }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/save-innovative-download-satisfaction${this.generalService.generateSearchQuery(
        data
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Make Personalized Content Request
   * @param data
   */
  personalizedRequest(data: {
    tranx_status: string;
    order_ref_id: string;
    order_type: string;
    total_cost: any;
    class_id: string;
    subject_id: string;
    topic: string;
  }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/save-personalized-content-request${this.generalService.generateSearchQuery(
        data
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Clears Users Saved item data
   */
  clearSavedItems() {
    return this.httpClient.get(`${environment.baseURL}/delete-saved-content`, <
      Object
    >{ responseType: "text/xml" }) as Observable<any>;
  }

  /**
   * Clears Users Personalized content
   */
  clearPersonalizedContent() {
    return this.httpClient.get(
      `${environment.baseURL}/delete-personalized-content-order-list`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }
  /**
   * Saves Content Id for Personlized conent request
   * @param isLessonPlan informs if it is a lesson plan being saved
   * @param id either the lesson id or the innovative id, depending on the content
   */
  saveContentId(isLessonPlan: boolean, id: string) {
    localStorage.setItem("contentId", JSON.stringify({ isLessonPlan, id }));
  }

  /**
   * Gets the saved Viewed Content Id
   * @returns  contentId and type -can be lesson plan id or innovative id
   */
  getContentIdInfo(): Object {
    return JSON.parse(localStorage.getItem("contentId"));
  }

  /**
   * Removes the stored content id from the browser
   */
  clearsContentId(): void {
    localStorage.removeItem("contentId");
  }
}
