import {
  Component,
  OnInit,
  ViewChild,
  ViewChildren,
  QueryList,
} from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { appRoutes } from "src/app/config";
import { environment } from "src/environments/environment";
import { GeneralService, AuthService } from "src/app/@core";
import { DashboarService } from "src/app/@core/dashboard.service";
import { ToastrService } from "ngx-toastr";
import { MatDialog } from "@angular/material/dialog";
declare let PaystackPop: any;

@Component({
  selector: "app-personalized-content",
  templateUrl: "./personalized-content.component.html",
  styleUrls: [
    "../settings/settings.component.scss",
    "../saved-items/saved-items.component.scss",
    "./personalized-content.component.scss",
  ],
})
export class PersonalizedContentComponent implements OnInit {
  dataSource: MatTableDataSource<any>;
  requestDataSource: MatTableDataSource<any>;

  @ViewChildren(MatPaginator) paginators: QueryList<MatPaginator>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  user: any;

  pageLength = 5;
  page = 0;
  requestPage = 0;
  searchedData = [];
  requestSearchedData = [];
  routes = appRoutes;
  category = environment.categoryTypes;
  personalizedData = [];
  requestData = [];
  hasLoaded = false;
  dialogRef: any;

  request: {
    class?: string;
    topic?: string;
    type?: string;
    subject?: string;
  } = {};
  classes = [];
  allSubjects = [];
  personlizedCost: any;
  amount: any;
  orderRef: string;

  constructor(
    private dashboardService: DashboarService,
    public generalService: GeneralService,
    private authService: AuthService,
    private service: DashboarService,
    private toastr: ToastrService,
    private dialog: MatDialog
  ) {}

  ngOnInit() {
    this.getClasses();
    this.getSubjects();
    this.getUserInfo();
    this.getPersonalizedContent();
    this.getRequests();
    this.getPersonalizedContentCost();
  }

  getPersonalizedContentCost() {
    this.dashboardService.getPersonlaizedContetnCost().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status;

          this.personlizedCost = this.generalService.convertToOneLayerViewModel(
            data
          );
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  getClasses() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllClasses().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.classes = data.list.class;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSubjects() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllSubjects().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.allSubjects = data.list.subject;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getRequests() {
    this.generalService.showLoader(true);
    this.service.getPersonalisedRequestList().subscribe(
      (data) => {
        this.generalService.showLoader(false);

        if (data.status._text != "Failed") {
          delete data.status._text;
          if (data.order_list) {
            this.requestData = [].concat(data.order_list.order).map((x) => {
              return this.generalService.convertToOneLayerViewModel(x);
            });
          }
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
        this.hasLoaded = true;
      },
      (error) => {
        this.hasLoaded = true;
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getPersonalizedContent() {
    this.generalService.showLoader(true);
    this.service.getPersonalisedList().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status._text;
          const list = [];
          if (data.order_list) {
            if (data.order_list.order) {
              data.order_list.order = data.order_list.order
                ? [].concat(data.order_list.order)
                : [];
              data.order_list.order.forEach((pc) => {
                if (pc.content_list.innovative_content) {
                  let invContent = pc.content_list.innovative_content
                    ? [].concat(pc.content_list.innovative_content)
                    : [];

                  const newContentArr: any = invContent.map((x) => {
                    x.component.map((y) => {
                      y.lesson_id = x._attributes.lesson_id;
                      y.topic = {};
                      y.topic["_text"] = x._attributes.topic;
                      y.subject_name = {};
                      y.subject_name["_text"] = x._attributes.subject_name;
                      y.class_name = {};
                      y.class_name["_text"] = x._attributes.class_name;
                      y.hasSeenContent =
                        y._attributes.viewed.toLowerCase() == "false"
                          ? false
                          : true;
                    });
                    return x.component;
                  });

                  invContent = newContentArr.flat();
                  for (let i = 0; i < invContent.length; i++) {
                    const innoComp = invContent[i]._attributes.short_name;

                    invContent[i].category = innoComp;

                    list.push(invContent[i]);
                  }
                }

                if (pc.content_list.lesson_plan) {
                  const lp = pc.content_list.lesson_plan;
                  lp.category = "Lesson";
                  lp.hasSeenContent =
                    lp._attributes.viewed.toLowerCase() == "false"
                      ? false
                      : true;

                  list.push(lp);
                }
              });
            }
          }

          // Assign the data to the data source for the table to render
          this.generalService.showLoader(false);
          this.personalizedData = list.sort((a, b) =>
            a.hasSeenContent === b.hasSeenContent ? 0 : a ? -1 : 1
          );
          this.dataSource = new MatTableDataSource(this.personalizedData);
          this.dataSource.paginator = this.paginators[0];
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();
  }

  open(templateRef) {
    this.dialogRef = this.dialog.open(templateRef, {
      id: "personalised-content-box",
    });
  }

  openPCdoneModal(template: any) {
    this.getRequests();
    this.dialog.open(template, {
      disableClose: true,
      id: "confirm-box2",
    });
  }

  paymentCallback(response) {
    if (response.status != "success") {
      this.toastr.error("Payment failed please try again later");
      this.generalService.showLoader(false);
      return;
    }
    this.generalService.showLoader(true);

    this.requestPersonalizedContent(response.status);
  }

  payWithPaystack() {
    this.generalService.showLoader(true);
    this.amount =
      this.request.type == "1"
        ? this.personlizedCost.lp_cost
        : this.request.type == "2"
        ? this.personlizedCost.tbt_cost
        : this.personlizedCost.both_cost;
    this.orderRef = `${new Date().getTime()}pers-${this.request.type}`;
    let paystack = PaystackPop;
    let handler = paystack.setup({
      key: environment.paystackPubKey,
      email: this.authService.getUserLocally().email,
      amount: this.amount * 100, // amount in kobo
      currency: "NGN",
      ref: this.orderRef,
      callback: (response) => {
        paystack = {};
        this.paymentCallback(response);
      },
      onClose: () => {
        paystack = {};
        this.generalService.showLoader(false);
        this.generalService.showLoader(false);
        this.toastr.error("Payment Cancelled");
      },
    });
    handler.openIframe();
  }

  requestPersonalizedContent(paystackResponse: string) {
    this.generalService.showLoader(false);
    this.dashboardService
      .personalizedRequest({
        tranx_status: paystackResponse,
        order_ref_id: this.orderRef,
        order_type: this.request.type,
        total_cost: this.amount,
        class_id: this.request.class,
        subject_id: this.request.subject,
        topic: this.request.topic,
      })
      .subscribe(
        (data) => {
          this.generalService.showLoader(false);
          if (data.status._text != "Failed") {
            delete data.status._text;
            this.dialog.closeAll();
            // this.toastr.success(
            //   "Your Personalized content request is successfull"
            // );
            document.getElementById("pcModalBtn").click();
            this.generalService.showLoader(false);
          } else {
            this.toastr.error(data.status_message._text, "Oops");
            this.generalService.showLoader(false);
          }
        },
        (error) => {
          this.generalService.showLoader(false);
          this.generalService.showLoader(false);
          this.toastr.error("Something went wrong");
        }
      );
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.searchedData = this.personalizedData.filter((x) =>
      x.topic._text.toLowerCase().includes(filterValue.toLowerCase())
    );
    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource(
      this.searchedData.length > 0 ? this.searchedData : this.personalizedData
    );
    this.dataSource.paginator = this.paginators[0];
  }

  applyRequestFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.requestSearchedData = this.requestData.filter((x) =>
      x.topic.toLowerCase().includes(filterValue.toLowerCase())
    );
    // Assign the data to the data source for the table to render
    this.requestDataSource = new MatTableDataSource(
      this.requestSearchedData.length > 0
        ? this.requestSearchedData
        : this.requestData
    );
    this.requestDataSource.paginator = this.paginators[1];
  }
}
