import { Subscription } from "rxjs";
import { Component, OnInit, OnDestroy } from "@angular/core";
import { DashboarService } from "src/app/@core/dashboard.service";
import { ToastrService } from "ngx-toastr";
import { GeneralService, AuthService } from "src/app/@core";
import { Router, ActivatedRoute } from "@angular/router";
import { appRoutes } from "src/app/config";
import { MatDialog } from "@angular/material/dialog";
import { PlanSubscriptionService } from "src/app/@core/plans-subscription.service";
import { environment } from "src/environments/environment";
declare let PaystackPop: any;

@Component({
  selector: "app-search-component",
  templateUrl: "./search-component.component.html",
  styleUrls: [
    "../dashboard/dashboard.component.scss",
    "./search-component.component.scss",
  ],
})
export class SearchBoxComponent implements OnInit, OnDestroy {
  classes: any;
  flipMe = false;
  selectOpened = false;
  showPersonalizedBox = false;
  subjects: any;
  search: any = {};
  request: {
    class?: string;
    topic?: string;
    type?: string;
    subject?: string;
  } = {};
  updates: Array<any> = [];
  showSearchBox = false;
  date = new Date();
  lessonsPlans: any;
  suggestionList: Array<{ _text: string }> = [];
  timeOut: any;
  boxTimeOut: any;
  isContentPage = false;
  contentId: string;
  shouldCheckSatisfaction = true;
  contentStateSub$: Subscription;
  personlizedCost: any;
  allSubjects: any;
  orderRef: string;
  amount: any;
  dialogRef: any;
  loading: boolean;

  constructor(
    private dashboardService: DashboarService,
    private toastr: ToastrService,
    private dialog: MatDialog,
    private router: Router,
    private route: ActivatedRoute,
    private generalService: GeneralService,
    private authService: AuthService,
    private paymentService: PlanSubscriptionService
  ) {}

  ngOnDestroy(): void {
    clearTimeout(this.timeOut);
  }

  ngOnInit(): void {
    this.getClasses();
    this.getSubjects();
    this.getLiveUpdates();
    this.getSearchCriteria();
    this.getPersonalizedContentCost();
  }

  closeBox() {
    clearTimeout(this.boxTimeOut);
    this.boxTimeOut = setTimeout(() => {
      this.selectOpened
        ? (this.showSearchBox = true)
        : (this.showSearchBox = false);
    }, 200);
  }

  getPersonalizedContentCost() {
    this.dashboardService.getPersonlaizedContetnCost().subscribe(
      (data) => {
        if (data.status._text != "Failed") {
          delete data.status;
          this.personlizedCost =
            this.generalService.convertToOneLayerViewModel(data);
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSearchCriteria() {
    this.route.queryParamMap.subscribe((param: any) => {
      if (Object.values(param.params).length > 0) {
        this.showSearchBox = false;
        Object.assign(this.search, param.params);
      }
    });
    this.route.url.subscribe((param: any) => {
      delete this.contentId;
      this.showSearchBox = false;
      this.flipMe = false;
      this.showPersonalizedBox = false;
      this.shouldCheckSatisfaction = true;
      if (this.contentStateSub$) this.contentStateSub$.unsubscribe();
      this.getUserContentSatisfaction();

      if (this.router.url.includes("page/s/search/")) {
        this.isContentPage = true;
      } else {
        this.isContentPage = false;
      }
    });
  }

  log(obj) {
    console.log({ obj });
  }

  saveContentSatisfaction(isSatisfied) {
    const contentInfo: any = this.dashboardService.getContentIdInfo();

    if (isSatisfied) {
      this.generalService.likeContent();
    }
    if (contentInfo.isLessonPlan) {
      this.contentStateSub$ = this.dashboardService
        .lessonPlanSatisfactory({
          lesson: contentInfo.id,
          satisfied: isSatisfied ? 2 : 1,
        })
        .subscribe(
          () => {
            this.toastr.success("Thank you for your feedback");
            if (isSatisfied) this.shouldCheckSatisfaction = false;
          },
          () => {
            if (isSatisfied) this.shouldCheckSatisfaction = false;
          }
        );
    } else {
      this.dashboardService
        .InnovativeContentSatisfactory({
          innovative_id: contentInfo.id,
          satisfied: isSatisfied ? 2 : 1,
        })
        .subscribe(
          () => {
            this.toastr.success("Thank you for your feedback");
            if (isSatisfied) this.shouldCheckSatisfaction = false;
          },
          () => {
            if (isSatisfied) this.shouldCheckSatisfaction = false;
          }
        );
    }
  }

  getUserContentSatisfaction() {
    this.generalService.contentInfoState$.subscribe((data) => {
      if (Object.keys(data).length > 1)
        this.dashboardService
          .getUserSatisfactory({
            data_id: data.id,
            data_type: data.isLessonPlan ? 1 : 2,
          })
          .subscribe((data) => {
            if (data.status_message._text.toLowerCase().includes("satisfied")) {
              this.shouldCheckSatisfaction = false;
            }
          });
    });
  }

  getSuggestions(keyword) {
    clearTimeout(this.timeOut);
    this.timeOut = setTimeout(() => {
      this.suggestionList = [];
      this.dashboardService.getSuggestions(keyword).subscribe(
        (data) => {
          if (data.status._text != "Failed") {
            delete data.status._text;

            this.suggestionList = this.suggestionList.concat(
              data.suggestions.suggestion
            );
          } else {
            // this.toastr.error(data.status_message._text, "Oops");
          }
        },
        (error) => {
          // this.toastr.error("Something went wrong");
        }
      );
    }, 1000);
  }

  getSubjects() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllSubjects().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.subjects = data.list.subject;
          this.allSubjects = this.subjects;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  paymentCallback(response) {
    if (response.status != "success") {
      this.toastr.error("Payment failed please try again later");
      this.generalService.showLoader(false);
      return;
    }
    this.generalService.showLoader(true);

    this.requestPersonalizedContent(response.status);
  }

  payWithPaystack() {
    this.loading = true;
    this.amount =
      this.request.type == "1"
        ? this.personlizedCost.lp_cost
        : this.request.type == "2"
        ? this.personlizedCost.tbt_cost
        : this.personlizedCost.both_cost;
    this.orderRef = `${new Date().getTime()}pers-${this.request.type}`;
    let paystack = PaystackPop;
    let handler = paystack.setup({
      key: environment.paystackPubKey,
      email: this.authService.getUserLocally().email,
      amount: this.amount * 100, // amount in kobo
      currency: "NGN",
      ref: this.orderRef,
      callback: (response) => {
        paystack = {};
        this.paymentCallback(response);
      },
      onClose: () => {
        paystack = {};
        this.loading = false;
        this.generalService.showLoader(false);
        this.toastr.error("Payment Cancelled");
      },
    });
    handler.openIframe();
  }

  requestPersonalizedContent(paystackResponse: string) {
    this.loading = false;
    this.dashboardService
      .personalizedRequest({
        tranx_status: paystackResponse,
        order_ref_id: this.orderRef,
        order_type: this.request.type,
        total_cost: this.amount,
        class_id: this.request.class,
        subject_id: this.request.subject,
        topic: this.request.topic,
      })
      .subscribe(
        (data) => {
          this.generalService.showLoader(false);
          if (data.status._text != "Failed") {
            delete data.status._text;
            this.dialog.closeAll();
            // this.toastr.success(
            //   "Your Personalized content request is successfull"
            // );
            document.getElementById("pcModalBtn").click();
            this.loading = false;
          } else {
            this.toastr.error(data.status_message._text, "Oops");
            this.loading = false;
          }
        },
        (error) => {
          this.loading = false;
          this.generalService.showLoader(false);
          this.toastr.error("Something went wrong");
        }
      );
  }

  getObjectKeys(obj) {
    return Object.keys(obj);
  }

  openPCdoneModal(template: any) {
    this.dialog.open(template, {
      disableClose: true,
      id: "confirm-box3",
    });
  }

  getClasses() {
    this.generalService.showLoader(true);
    this.dashboardService.getAllClasses().subscribe(
      (data) => {
        console.log(data.list.class);
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;
          this.classes = data.list.class;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSubjectsForSelectedClasses(className: { value: string }) {
    if (className.value) {
      this.generalService.showLoader(true);
      this.dashboardService.getAllSubjectsForClass(className.value).subscribe(
        (data) => {
          this.generalService.showLoader(false);
          if (data.status._text != "Failed") {
            delete data.status._text;
            this.subjects = data.list.subject;
          } else {
            this.toastr.error(data.status_message._text, "Oops");
          }
        },
        (error) => {
          this.generalService.showLoader(false);
          this.toastr.error("Something went wrong");
        }
      );
    } else {
      this.getSubjects();
    }
  }

  getLiveUpdates() {
    this.generalService.showLoader(true);
    this.dashboardService.getContentUpdate().subscribe((data) => {
      this.generalService.showLoader(false);
      if (data.status._text != "Failed") {
        delete data.status._text;

        this.updates = data.list.content;
      } else {
        this.generalService.showLoader(false);
        this.toastr.error(data.status_message._text, "Oops");
      }
    });
  }

  goToSearchPage() {
    this.router.navigate([appRoutes.searchPage], { queryParams: this.search });
  }

  open(templateRef) {
    this.dialogRef = this.dialog.open(templateRef, {
      id: "personalised-content-box",
    });
  }
}
