import { MENU } from "./pages.menu";
import { Component, OnInit, OnDestroy } from "@angular/core";
import { Subscription } from "rxjs";
import { ToastrService } from "ngx-toastr";
import { AuthService, GeneralService } from "src/app/@core";
import { User } from "src/app/@model";
import { appRoutes } from "src/app/config";
import { Router } from "@angular/router";

@Component({
  selector: "app-page",
  templateUrl: "./page.component.html",
  styleUrls: ["./page.component.scss"],
})
export class PageComponent implements OnInit, OnDestroy {
  loading: boolean;
  loadingSubscription: Subscription;
  userEmail: string;
  user: User;
  timePeriod: string;

  constructor(
    private toastr: ToastrService,
    private authService: AuthService,
    private generalService: GeneralService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getGreetingTime();
    this.getUserInfo();
    this.checkForLoadingState();
  }

  checkForLoadingState() {
    this.loadingSubscription = this.generalService.state$.subscribe((arg) => {
      this.loading = arg;
    });
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();
  }

  getGreetingTime() {
    let greeting = null;
    const splitAfternoon = 12; //24hr time to split the afternoon
    const splitEvening = 17; //24hr time to split the evening
    const currentHour = new Date().getHours();

    if (currentHour >= splitAfternoon && currentHour <= splitEvening) {
      greeting = "afternoon";
    } else if (currentHour >= splitEvening) {
      greeting = "evening";
    } else {
      greeting = "morning";
    }

    this.timePeriod = greeting;
  }

  visible($event) {
    let dropDownClass = $event.currentTarget.children[1].className;
    dropDownClass = dropDownClass.includes("visible")
      ? dropDownClass.replace(" visible", "")
      : dropDownClass + " visible";

    $event.currentTarget.children[1].className = dropDownClass;
  }

  keepVisible($event) {
    $event.currentTarget.children[1].className = "dropdown visible";
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    //this.loadingSubscription.unsubscribe();
  }
}
