import { environment } from "./../../../environments/environment.prod";
import { appRoutes } from "./../../config";
import { ToastrService } from "ngx-toastr";
import { DashboarService } from "src/app/@core/dashboard.service";
import { GeneralService, AuthService } from "src/app/@core";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";

@Component({
  selector: "app-saved-items",
  templateUrl: "./saved-items.component.html",
  styleUrls: [
    "../settings/settings.component.scss",
    "./saved-items.component.scss",
  ],
})
export class SavedItemsComponent implements OnInit {
  savedItems = [];
  dataSource: MatTableDataSource<any>;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  user: any;

  pageLength = 10;
  page = 0;
  searchedData = [];
  routes = appRoutes;
  category = environment.categoryTypes;

  constructor(
    private generalService: GeneralService,
    private authService: AuthService,
    private service: DashboarService,
    private toastr: ToastrService
  ) {}

  ngOnInit() {
    this.getSavedInnovativeComponent();
    this.getSavedLessonPlan();
    this.getUserInfo();
  }

  getSavedLessonPlan() {
    this.generalService.showLoader(true);
    this.service.getSavedLessonPlans().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (
          data.status._text != "Failed" &&
          data.status._text != "Null" &&
          data.status._text
        ) {
          delete data.status._text;
          if (!this.savedItems) {
            this.savedItems = data.saved_contents.lesson_file;
          } else {
            this.savedItems = this.savedItems.concat(
              data.saved_contents.lesson_file
            );
          }
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.savedItems);
          this.dataSource.paginator = this.paginator;
        } else if (data.status._text != "Null") {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSavedInnovativeComponent() {
    this.generalService.showLoader(true);
    this.service.getSavedInnovativeContent().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (
          data.status._text != "Failed" &&
          data.status._text != "Null" &&
          data.status._text
        ) {
          delete data.status._text;
          if (!this.savedItems) {
            this.savedItems = data.saved_contents.content;
          } else {
            this.savedItems = this.savedItems.concat(
              data.saved_contents.content
            );
          }
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.savedItems);
          this.dataSource.paginator = this.paginator;
        } else if (data.status._text != "Null") {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.searchedData = this.savedItems.filter((x) =>
      x._attributes.topic.toLowerCase().includes(filterValue.toLowerCase())
    );
    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource(
      this.searchedData.length > 0 ? this.searchedData : this.savedItems
    );
    this.dataSource.paginator = this.paginator;
  }
}
