import { environment } from "src/environments/environment";
import { appRoutes } from "src/app/config";
import { GeneralService } from "./../../@core/general.service";
import { DashboarService } from "./../../@core/dashboard.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { MatDialog } from "@angular/material/dialog";
import { PlanSubscriptionService } from "src/app/@core/plans-subscription.service";

@Component({
  selector: "app-search-page",
  templateUrl: "./search-page.component.html",
  styleUrls: [
    "../dashboard/dashboard.component.scss",
    "./search-page.component.scss",
  ],
})
export class SearchPageComponent implements OnInit {
  searchParam: {
    topic?: string;
    class?: string;
    subject?: string;
    keyword?: string;
  } = {};
  categories = [];
  selectedCategory = "all";
  lessonsPlans: any = [];
  innovativeContents = {};
  selectedPlans: any = [];
  allPlans: any = [];
  routes = appRoutes;

  categoryTypes = environment.categoryTypes;

  scrolledCount: any = {};
  suggestedTopics: any[] = [];
  recentlyViewed: any[] = [];
  hasSubExpired: boolean = true;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private toastr: ToastrService,
    private service: DashboarService,
    private generalService: GeneralService,
    public dialog: MatDialog,
    private subscriptionService: PlanSubscriptionService
  ) {}

  ngOnInit(): void {
    this.getSearchCriteria();
    this.checkIfUserIsSubscribed();
  }

  searchLessonPlans() {
    this.generalService.showLoader(true);

    this.service.getLessonPlan(this.searchParam).subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;

          this.lessonsPlans = data.list.lesson_plan;
          this.lessonsPlans.forEach((plan) => {
            plan.category = "Lesson plan";
            plan.badgeColor = "bg-success";
            plan.pictures = Object.values(plan.lesson_pictures).map(
              (x: any) => x._text
            );
          });
          this.getAllPlans(this.lessonsPlans);
        } else {
          // this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSuggestionsPlans() {
    this.suggestedTopics = [];
    this.searchParam.topic = this.searchParam.keyword;
    const query = {
      topic: this.searchParam.topic,
      subject: this.searchParam.subject,
      page: 2,
    };
    this.service.getSmartSearch(query).subscribe(
      (data) => {
        const innovativeContent = {};
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status;

          const lessonPlan = data.search_list.lesson_plan
            ? [].concat(data.search_list.lesson_plan)
            : [];

          lessonPlan.forEach((plan) => {
            plan.category = "Lesson plan";
            plan.badgeColor = "bg-success";
            plan.isFree = plan.free_status._text === "FREE";
            plan.pictures = Object.values(plan.lesson_pictures).map(
              (x: any) => x._text
            );
          });

          if (data.search_list.innovative_content) {
            let invContent = data.search_list.innovative_content
              ? [].concat(data.search_list.innovative_content)
              : [];

            const newContentArr: any = invContent.map((x) => {
              x.component.map((y) => {
                y.lesson_id = x._attributes.lesson_id;
                y.topic = x._attributes.topic;
                y.class_name = x._attributes.class_name;
                y.isFree = x._attributes.free_status === "FREE";
              });

              return x.component;
            });
            invContent = newContentArr.flat();
            for (let i = 0; i < invContent.length; i++) {
              const innoComp = invContent[i]._attributes.short_name;
              if (!innovativeContent[innoComp]) {
                innovativeContent[innoComp] = [];
              }
              invContent[i].category = innoComp;
              invContent[i].pictures = invContent[i].contents.images
                ? invContent[i].contents.images._text.split(",")
                : "/assets/images/logo.png";
              invContent[i].badgeColor = this.getInnovativeContentBadgeColor(
                innoComp
              );
              innovativeContent[innoComp].push(invContent[i]);
            }
            this.suggestedTopics = lessonPlan.concat(
              Object.values(innovativeContent)[0]
            );
          }
        } else {
          // this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getInnovativeContentBadgeColor(category: string) {
    switch (category) {
      case "ITMCA":
        return "bg-multimedia";

      case "IRM":
        return "bg-warning";
      case "PEC":
        return "bg-orange";

      case "IIET":
        return "bg-info";

      default:
        break;
    }
  }

  getAllPlans(plans: Array<any>) {
    this.allPlans = this.allPlans.concat(plans);
    this.pickACategory();
  }

  pickACategory(category = "all") {
    this.selectedCategory = category;
    switch (category) {
      case "lesson":
        this.selectedPlans = this.lessonsPlans;
        break;
      case "ITMCA":
      case "IRM":
      case "PEC":
      case "IIET":
        this.selectedPlans = this.innovativeContents[category] || [];
        break;
      case "all":
        this.selectedPlans = this.allPlans;
        break;

      default:
        this.selectedPlans = [];
        break;
    }
  }

  searchInnovativeContents() {
    const innovativeContent = {};

    this.generalService.showLoader(true);
    this.service.getInnovativeContent(this.searchParam).subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status._text;

          for (let i = 0; i < data.list.innovative_content.length; i++) {
            const innoComp = data.list.innovative_content[
              i
            ].component_name._text
              .replace("using", "")
              .replace("tools", "")
              .replace("and", "")
              .replace("topics", "")
              .replace("skills", "")
              .replace(",", "")
              .toUpperCase()
              .split(" ")
              .map((x) => x[0])
              .join("");
            if (!innovativeContent[innoComp]) {
              innovativeContent[innoComp] = [];
            }
            data.list.innovative_content[i].category = innoComp;
            data.list.innovative_content[i].badgeColor = "bg-warning";
            innovativeContent[innoComp].push(data.list.innovative_content[i]);
          }
          this.getAllPlans(data.list.innovative_content);
          console.log(innovativeContent);
          this.innovativeContents = innovativeContent;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSmartSearch(searchQuery: object) {
    this.generalService.showLoader(true);
    this.lessonsPlans = [];
    this.innovativeContents = {};
    this.service.getSmartSearch(searchQuery).subscribe(
      (data) => {
        const innovativeContent = {};
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status;

          const lessonPlan = data.search_list.lesson_plan
            ? [].concat(data.search_list.lesson_plan)
            : [];

          lessonPlan.forEach((plan) => {
            plan.category = "Lesson plan";
            plan.badgeColor = "bg-success";
            plan.isFree = plan.free_status._text === "FREE";
            plan.pictures = Object.values(plan.lesson_pictures).map(
              (x: any) => x._text
            );
          });
          this.lessonsPlans = lessonPlan;

          this.getAllPlans(this.lessonsPlans);

          if (data.search_list.innovative_content) {
            let invContent = data.search_list.innovative_content
              ? [].concat(data.search_list.innovative_content)
              : [];

            const newContentArr: any = invContent.map((x) => {
              x.component.map((y) => {
                y.lesson_id = x._attributes.lesson_id;
                y.topic = x._attributes.topic;
                y.class_name = x._attributes.class_name;
                y.isFree = x._attributes.free_status === "FREE";
              });

              return x.component;
            });
            invContent = newContentArr.flat();

            for (let i = 0; i < invContent.length; i++) {
              const innoComp = invContent[i]._attributes.short_name;
              if (!innovativeContent[innoComp]) {
                innovativeContent[innoComp] = [];
              }
              invContent[i].category = innoComp;

              invContent[i].pictures = invContent[i].contents.images
                ? invContent[i].contents.images._text.split(",")
                : "/assets/images/logo.png";
              invContent[i].badgeColor = this.getInnovativeContentBadgeColor(
                innoComp
              );

              innovativeContent[innoComp].push(invContent[i]);
            }
            this.getAllPlans(invContent);
            this.innovativeContents = innovativeContent;
          }
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  getSearchCriteria() {
    this.route.queryParamMap.subscribe((param: any) => {
      Object.assign(this.searchParam, param.params);
      this.getSmartSearch(this.searchParam);
      this.getSuggestionsPlans();
      this.getRecentlyViewed();
      this.allPlans = [];
      this.suggestedTopics = [];
      //this.searchInnovativeContents();
    });
  }

  openIfUserHasSubscription(plan, template) {
    if (!plan.isFree && this.hasSubExpired) {
      this.openModal(template);
      return;
    }
    this.router.navigate([
      this.routes.viewPlan,
      plan.category,
      plan.lesson_id._text ? plan.lesson_id._text : plan.lesson_id,
    ]);
  }

  scrollHorizontal(event, key) {
    if (!this.scrolledCount[key]) {
      this.scrolledCount[key] = 1;
    }
    const containerDiv = event.currentTarget.parentElement.children[0];
    containerDiv.scrollTo(
      containerDiv.offsetWidth * this.scrolledCount[key] - 150,
      0
    );
    this.scrolledCount[key]++;
  }

  openModal(template: any) {
    this.dialog.open(template, {
      disableClose: true,
      id: "confirm-box4",
    });
  }

  redirectUserTOSubPage() {
    this.dialog.closeAll();
    this.router.navigate([this.routes.subscription]);
  }

  getRecentlyViewed() {
    this.recentlyViewed = [];
    this.service.getRecentlyViewed().subscribe(
      (data) => {
        this.generalService.showLoader(false);
        if (data.status._text != "Failed") {
          delete data.status;

          const recentlyViewed = data.recent_list
            ? ([].concat(data.recent_list.viewed_content) as Array<any>)
            : [];
          recentlyViewed.forEach((plan) => {
            if (plan._attributes.type.toLowerCase() === "lesson plan") {
              plan.category = "Lesson plan";
              plan.badgeColor = "bg-success";

              plan.free_status._text === "FREE";
              plan.pictures = Object.values(plan.lesson_pictures).map(
                (x: any) => x._text
              );
            } else {
              plan.category = plan._attributes.component_short_name;
              plan.badgeColor = this.getInnovativeContentBadgeColor(
                plan.category
              );
              plan.lesson_id = plan._attributes.lesson_id;
              plan.class_name = plan._attributes.class_name;
              plan.subject_name = plan._attributes.subject_name;
              plan.topic = plan._attributes.topic;
              plan.pictures = plan.contents.images
                ? plan.contents.images._text.split(",")
                : [`/assets/images/${plan.category}/1.png`];
            }
          });

          this.recentlyViewed = recentlyViewed;
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }

  checkIfUserIsSubscribed() {
    this.generalService.showLoader(true);
    this.subscriptionService.getUsersSubscriptionStatus().subscribe(
      (data) => {
        //  data = this.generalService.xmlToJson(subscribedUser);
        this.generalService.showLoader(false);
        if (data.status._text && data.status._text != "Failed") {
          this.hasSubExpired = data.status._text == "Expired";
        } else {
          this.toastr.error(data.status_message._text, "Oops");
        }
      },
      (error) => {
        this.generalService.showLoader(false);
        this.toastr.error("Something went wrong");
      }
    );
  }
}
