import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "src/environments/environment";
import { Observable } from "rxjs";
import { GeneralService } from "./general.service";

@Injectable({ providedIn: "root" })
export class PlanSubscriptionService {
  constructor(
    private httpClient: HttpClient,
    private generalService: GeneralService
  ) {}

  /**
   * Get Single subscription details from API
   */
  getSingleSubscription() {
    return this.httpClient.get(
      `${environment.baseURL}/single-subscription-packages`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Get Muilt users subscription details
   */
  getMultiSubscription() {
    return this.httpClient.get(
      `${environment.baseURL}/multi-subscription-packages`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Returns package config for how long a term or a session will last
   */
  getPackageConfig() {
    return this.httpClient.get(`${environment.baseURL}/package-config`, <
      Object
    >{
      responseType: "text/xml",
    }) as Observable<any>;
  }

  /**
   * Creates a subscription fo the User
   * @param arg0 object prams
   */
  createSubscription(sub: {
    package_id: any;
    total_cost: any;
    month_count: any;
  }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/create-new-subscription${this.generalService.generateSearchQuery(sub)}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * Creates a subscription fo the User
   * @param arg0 object prams
   */
  confirmSubscriptionOrder(sub: {
    order_no: any;
    subscription_id: any;
    tranx_status: any;
  }) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/confirm-subscription-payment${this.generalService.generateSearchQuery(
        sub
      )}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  /**
   * get Users subscription status
   */
  getUsersSubscriptionStatus() {
    return this.httpClient.get(`${environment.baseURL}/user-subscription`, <
      Object
    >{
      responseType: "text/xml",
    }) as Observable<any>;
  }

  deleteEmailFromSubscription(email, subscriptionId: string = "") {
    return this.httpClient.get(
      `${environment.baseURL}/delete-subscription-user?email=${email}&subscription_id=${subscriptionId}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  getEmailsOnSubscription(subscriptionId: string = "") {
    return this.httpClient.get(
      `${environment.baseURL}/get-subscription-users?subscription_id=${subscriptionId}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }

  addEmailsToSubscription(emails: Array<string>, subscriptionId: string = "") {
    let emailQuery = "";
    emails.forEach((email, i) => {
      emailQuery = emailQuery + `emails[${i}]=${email}&`;
    });
    return this.httpClient.get(
      `${environment.baseURL}/add-subscription-user?${emailQuery}subscription_id=${subscriptionId}`,
      <Object>{
        responseType: "text/xml",
      }
    ) as Observable<any>;
  }
}
