import { AuthService } from "src/app/@core/auth.service";
import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  ElementRef,
  ViewChildren,
  QueryList,
  OnDestroy,
} from "@angular/core";
import { MenuItem } from "../interfaces/menu-item";
import { Router } from "@angular/router";
import { appRoutes } from "src/app/config";

@Component({
  selector: "[vgMenuItem]",
  templateUrl: "./vg-menu-item.component.html",
  styleUrls: ["./vg-menu-item.component.scss"],
})
export class VgMenuItemComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() menuItem = <MenuItem>null;

  @Output() toggleSubMenu = new EventEmitter<any>();
  @Output() selectItem = new EventEmitter<any>();
  @Output() itemClick = new EventEmitter<any>();
  private alive = true;

  @ViewChildren(VgMenuItemComponent, { read: ElementRef }) subMenu: QueryList<
    ElementRef
  >;
  constructor(private router: Router, private authService: AuthService) {}

  ngOnInit() {}

  ngAfterViewInit() {}

  onToggleSubMenu(item: MenuItem) {
    this.toggleSubMenu.emit(item);
  }

  onSelectItem(item: MenuItem) {
    this.selectItem.emit(item);
  }

  onItemClick(item: MenuItem) {
    this.itemClick.emit(item);
  }

  logOut() {
    const user = this.authService.getUserLocally();

    if (user.show_feedback === "TRUE") {
      document.getElementById("feedback").click();
      Object.assign(user, { show_feedback: "FALSE" });
      this.authService.saveUserLocally(user);
    } else {
      this.authService.logout();
      this.router.navigate([appRoutes.login]);
    }
  }

  ngOnDestroy() {
    this.alive = false;
  }
}
